import React from 'react';
import asm8086Lexer from "../../out/asm8086Lexer"
import asm8086Parser from "../../out/asm8086Parser"
import antlr4 from 'antlr4';
import AceEditor from "react-ace";
import "ace-builds/src-noconflict/mode-assembly_x86";
import "ace-builds/src-noconflict/theme-github";

class InputView extends React.Component{

    initialValue = "org     1000h       ; Start at 0000:1000h\n" +
        "\n" +
        "_memcpy     proc\n" +
        "            push    bp          ; Set up the call frame\n" +
        "            mov     bp,sp\n" +
        "            push    es          ; Save ES\n" +
        "            mov     cx,[bp+6]   ; Set CX = len\n" +
        "            jcxz    done        ; If len = 0, return\n" +
        "            mov     si,[bp+4]   ; Set SI = src\n" +
        "            mov     di,[bp+2]   ; Set DI = dst\n" +
        "            push    ds          ; Set ES = DS\n" +
        "            pop     es\n" +
        "\n" +
        "loop        mov     al,[si]     ; Load AL from [src]\n" +
        "            mov     [di],al     ; Store AL to [dst]\n" +
        "            inc     si          ; Increment src\n" +
        "            inc     di          ; Increment dst\n" +
        "            dec     cx          ; Decrement len\n" +
        "            jnz     loop        ; Repeat the loop\n" +
        "\n" +
        "done        pop     es          ; Restore ES\n" +
        "            pop     bp          ; Restore previous call frame\n" +
        "            sub     ax,ax       ; Set AX = 0\n" +
        "            ret                 ; Return\n" +
        "            end proc";

    constructor(props){
        super(props);
        this.state = {codeString : ""};
        this.parse = this.parse.bind(this)
    }

    parse = () =>{
        const editor = this.refs.reactAceComponent.editor;
        let codeString = editor.getValue()+'\n';
        var chars = new antlr4.InputStream(codeString);
        var lexer = new asm8086Lexer.asm8086Lexer(chars);
        var tokens  = new antlr4.CommonTokenStream(lexer);
        var parser = new asm8086Parser.asm8086Parser(tokens);
        parser.buildParseTrees = true;
        var tree = parser.prog();
        console.log("Parsed: "+ tree);
    };

    render() {
        return(
            <React.Fragment>

                <AceEditor
                    defaultValue={this.initialValue}
                    ref="reactAceComponent"
                    mode="assembly_x86"
                    theme="github"
                    setOptions={{
                        enableBasicAutocompletion: true,
                        enableLiveAutocompletion: true,
                        enableSnippets: false,
                        showLineNumbers: true,
                        tabSize: 5,
                    }}
                    placeHolder="Inserte su cdigo aqu"
                />

                <button onClick={this.parse}>Start</button>
            </React.Fragment>
        );
    }
}

export default InputView;